@use '../util' as *;

html {
	font-size: 100%;
	box-sizing: border-box;
	scroll-behavior: smooth;
}

*,
*::before,
*::after {
	box-sizing: inherit;
}

body {
	margin: 0;
	padding: 0;
	color: var(--text);
	letter-spacing: rem(1.6);
	font-family: var(--font-body);
}

.arrow {
	position: relative;
	display: inline-block;
	min-width: rem(53);
	min-height: rem(33);
	background: url('../images/tools/arrow-right.svg');

	&__left {
		margin-right: rem(42);
		background-image: url('../images/tools/arrow-light.svg');
	}
}

.direction {
	display: block;
	position: relative;
	top: rem(10);
	left: 50%;
	max-width: rem(54);
	transform: translateX(-50%) rotate(90deg);

	&::before {
		content: 'brws';
		position: absolute;
		right: rem(52);
		top: 50%;
        font-size: rem(24);
		font-family: var(--font-title);
		transform: translateY(-50%) rotate(-90deg);
	}

	&::after {
		content: '';
		position: absolute;
		z-index: -1;
		top: 50%;
		left: rem(-60);
		min-width: rem(100);
		min-height: rem(108);
		transform: translateY(-50%);
		background-color: var(--white);
		box-shadow: 0 rem(20) rem(40) var(--black);
	}

	&__border {
		position: absolute;
		z-index: -1;
		top: 50%;
		left: rem(45);
		min-width: rem(3);
		min-height: rem(495);
		pointer-events: none;
		transform: translateY(-50%);
		background-color: var(--black);

		&::before {
			content: '';
			position: absolute;
			top: 50%;
			left: 50%;
			min-width: rem(70);
			min-height: rem(178);
			transform: translate(-50%, -50%);
			border: rem(10) solid var(--black);
			background-color: var(--white);
		}

		&::after {
			content: '';
			position: absolute;
			top: rem(158.6);
			left: rem(36);
			min-width: rem(113);
			min-height: rem(660);
			border-top: rem(3) solid;
			border-right: rem(3) solid;
			border-color: var(--black);
		}
	}

	svg {
		fill: var(--black);
	}
}
